import {h, Component} from "preact";
import classNames  from "classnames";
import _ from 'lodash';
import React from "react";
import {Scrollbars} from "preact-custom-scrollbars";

const CARD_WIDTH = 208;

export default class Message extends Component {
    state = {
        open: false
    }

    constructor() {
        super(...arguments);
        this.close = this.close.bind(this);
    }
    shouldComponentUpdate(nextProps,nextState) {
        return this.props.message !== nextProps.message || this.props.allowButton !== nextProps.allowButton || this.state.open !== nextState.open
    }
    close(e) {
        if(e.data === 'close') {
            if (this.state.open) {
                this.setState({open: false})
            }
        }
    }
    componentDidMount() {
        window.addEventListener('message', this.close)
    }
    componentDidUnmount() {
        window.removeEventListener('message', this.close)
    }

    get buttons() {
        return this.props.allowButton && this.props.message.buttons
    }

    handleRightScrollClick = () => {
        if(this.list) {
            let left = this.list.getScrollLeft();
            if(this.list.getScrollWidth() - this.list.getScrollLeft() <= (this.list.getClientWidth()*2)) {
                console.log('end');
            }
            this.list.scrollLeft(left += CARD_WIDTH);
        }
    };

    getIconByFile(url) {
        let ext = url.split('.').pop();
        switch (ext) {
            case 'pdf':
                return 'pdf';
            case 'zip':
            case 'tar':
            case 'gz':
            case 'rar':
                return 'file-archive';
            default:
                return  'document'
        }
    }

    handleLeftScrollClick = () => {
        if(this.list) {
            let left = this.list.getScrollLeft();
            if(this.list.getScrollLeft() <= CARD_WIDTH) {
                console.log('start');
            }
            this.list.scrollLeft(left -= CARD_WIDTH);
        }
    };

    render() {
        const message = this.props.message;

        return (
            <div className={classNames("message-block", {"income": message.getIsIncome(), "outcome": message.getIsOutcome()})}>
                {message.getIsIncome() && this.props.showImage &&
                    <div className="image-block">
                        <img className={'image'} src={this.props.image ? this.props.image : message.image} alt=""/>
                    </div>
                }
                <div className="messages">
                    <div className="message-item">
                        <div className="message">
                            <div>
                                {message.message}
                            </div>
                            {
                                message.images && <div>
                                    {
                                        message.images.map((img) => {
                                            return <img style={{height: 100}} src={img.data.url} key={img.id} alt=""/>
                                        })
                                    }

                                </div>
                            }
                            {
                                message.files && <div>
                                    {
                                        message.files.map((file) => {
                                            return <div>
                                                <a href={file.data.url} target='_blank'>Attachment</a>

                                            </div>
                                        })
                                    }

                                </div>
                            }
                            {
                                message.cards.map((cards) => {

                                    if(cards.data.type === 'calendar') {
                                        const button =  cards.data.elements[0].buttons[0];
                                        return <div>
                                            {this.state.open && <div className={'message-modal'}>
                                                <iframe
                                                    src={button.url}
                                                    frameBorder={0}
                                                />
                                            </div>}
                                            <a
                                                className={'btn'}
                                                onClick={() => {
                                                   this.setState({
                                                       open: true
                                                   })
                                                }}
                                            >
                                                {button.title}
                                            </a>
                                        </div>;
                                    }

                                    return <div className={'tc'}>
                                        <div className={'tc__right'} onClick={this.handleRightScrollClick}><i className="icon-angle-right-solid" aria-hidden="true"/></div>
                                        <div className={'tc__left'} onClick={this.handleLeftScrollClick}><i className="icon-angle-left-solid" aria-hidden="true"/></div>
                                        <Scrollbars
                                            ref={(el) => this.list = el}
                                            renderThumbHorizontal={props => <div {...props} className="scroll-thumb-horizontal"/>}
                                            renderTrackHorizontal={props => <div {...props} className="scroll-track-horizontal"/>}
                                            renderView={props => <div {...props} className="tc__cards"/>}
                                        >
                                            {
                                                cards.data.elements.map((card, cardIndex) => {
                                                    const buttons = card.buttons || []
                                                    return <div key={cardIndex} className={classNames("tc__cards__card", {"hasImg": card.image_url})}>
                                                        {card.image_url && <div className={'tc__cards__card__imgBlock'}><img className={'tc__cards__card__imgBlock__img'} src={card.image_url} alt={card.title}/></div>}
                                                        <div className={'tc__cards__card__textBlock'}>
                                                            <div className={'tc__cards__card__textBlock__title'} dangerouslySetInnerHTML={{__html:card.title}}>{}</div>
                                                            <div className="tc__cards__card__textBlock__subtitle">
                                                            <Scrollbars
                                                                ref={(el) => this.list = el}
                                                                renderThumbVertical={props => <div {...props} className="scroll-thumb-vertical"/>}
                                                                renderTrackVertical={props => <div {...props} className="scroll-track-vertical"/>}
                                                                renderView={props => <div {...props} className="scroll-view"/>}
                                                            >
                                                                <div dangerouslySetInnerHTML={{__html:card.subtitle}}>{}</div>
                                                            </Scrollbars>
                                                            </div>
                                                            {buttons.length ?
                                                                <div className={'tc__cards__card__textBlock__btns'}>
                                                                    {buttons.map((button, buttonIndex) => {
                                                                        switch (button.type) {
                                                                            case 'web_url':
                                                                                return <a
                                                                                    key={buttonIndex}
                                                                                    className={'tc__cards__card__textBlock__btns__btn'}
                                                                                    target="_blank"
                                                                                    href={button.url}
                                                                                >
                                                                                    {button.title}
                                                                                </a>;
                                                                            case 'postback':
                                                                                return <a
                                                                                    key={buttonIndex}
                                                                                    onClick={() => this.props.sendMessage(button.title, null, button.payload)}
                                                                                    className={'tc__cards__card__textBlock__btns__btn'}
                                                                                >
                                                                                    {button.title}
                                                                                </a>;
                                                                        }
                                                                    })}
                                                                </div>
                                                                : ''
                                                            }
                                                        </div>
                                                    </div>
                                                })
                                            }
                                        </Scrollbars>
                                    </div>;
                                })
                            }

                            {
                                this.buttons && <div>
                                    {
                                        this.buttons.map(({text}) =>  <button className={'btn'} onClick={() => this.props.sendMessage(text)}>{text}</button>)
                                    }
                                </div>
                            }
                        </div>
                        <div className="time">
                            {this.props.time}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}



