,IACE,0HAAA,iHAAoH,CAGtH,IACE,2BAAA,kBAAmB,CAAA,CAIvB,+BACE,kBACE,WACA,YACA,SACA,SAAU,CAGZ,eACE,WACA,YACA,OAAQ,CAGV,GACE,gCAAA,uBAAwB,CAG1B,IACE,mCAAA,0BAA2B,CAE7B,IACE,mCAAA,0BAA2B,CAE7B,IACE,mCAAA,0BAA2B,CAE7B,GACE,gCAAA,uBAAwB,CAAA,CA5B5B,uBACE,kBACE,WACA,YACA,SACA,SAAU,CAGZ,eACE,WACA,YACA,OAAQ,CAGV,GACE,gCAAA,uBAAwB,CAG1B,IACE,mCAAA,0BAA2B,CAE7B,IACE,mCAAA,0BAA2B,CAE7B,IACE,mCAAA,0BAA2B,CAE7B,GACE,gCAAA,uBAAwB,CAAA,CAAA","file":"style.scss","sourcesContent":["@import \"glyphter.css\";\n\nbody {\n  display: inline-block;\n}\n\n:root {\n  --bubble-color: #009ce0;\n  --bubble-bg-color: #cccccc;\n  --bubble-rgb-color: rgba(0, 0, 0, 0.2);\n  --bubble-size: 60px;\n  \n  --window-position: right;\n  --window-width: 350px;\n  --window-height: 600px;\n  --window-header-height: 60px;\n  --window-footer-height: 40px;\n  --window-body-height: 500px;\n  \n  --header-color: white;\n  --header-bg-color: #333333;\n  --header-font-size: 14px;\n  --header-font-family: \"sans-serif\";\n  \n  --window-body-color: white;\n  --window-body-bg-color: #393a4e;\n  \n  --message-income-bg-color: #2e1849;\n  --message-income-color: #ffffff;\n  --message-income-font-size: 12px;\n  --message-income-font-family: \"sans-serif\";\n  --message-income-btn-bg-color: #ffffff;\n  --message-income-btn-color: #000000;\n  --message-income-btn-font-size: 12px;\n  --message-income-btn-font-family: \"sans-serif\";\n  \n  --message-outcome-bg-color: #3195d2;\n  --message-outcome-color: #ffffff;\n  --message-outcome-font-size: 12px;\n  --message-outcome-font-family: \"sans-serif\";\n  \n  --textarea-bg-color: #000000;\n  --textarea-color: #ffffff;\n  --textarea-placeholedr-color: #ffffff;\n  --textarea-font-size: 14px;\n  --textarea-font-family: \"sans-serif\";\n  \n  --send-btn-bg-color: #ffffff;\n  --send-btn-color: #68bc00;\n  \n  --footer-color: white;\n  --footer-bg-color: #333333;\n  --footer-font-size: 14px;\n  --footer-font-family: \"sans-serif\";\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n  overflow: hidden;\n}\n\n.relative {\n  position: relative;\n}\n\n.window-wrapper {\n  transition: visibility 0s, opacity 0.5s linear;\n  width: var(--window-width);\n  height: var(--window-height);\n  margin: 10px 10px 0;\n  z-index: 2;\n  \n  &.fullscreen {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    margin: 0;\n  \n    .window-body {\n      height: calc(100vh - 100px);\n  \n      .messages-list {\n        height: calc(100vh - 160px)!important;\n        \n        &.has-files {\n          height: calc(100vh - 180px)!important;\n        }\n      }\n    }\n    \n    .message-modal {\n      left: 0;\n      right: 0;\n      top: 60px;\n      bottom: 40px;\n    }\n  }\n}\n\n.window-header {\n  height: var(--window-header-height);\n  background-color: var(--header-bg-color);\n  color: var(--header-color);\n  font-size: var(--header-font-size);\n  font-family: var(--header-font-family);\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 0 5px;\n  \n  .name {\n    display: flex;\n    align-items: center;\n    \n    .logo-img {\n      width: auto;\n      height: 40px;\n    }\n    \n    .title {\n      margin: 0 5px;\n    }\n  }\n  \n  .action-btn {\n    font-size: 14px;\n    cursor: pointer;\n    margin: 6px;\n    display: inline-block;\n  }\n}\n\n.window-body {\n  height: var(--window-body-height);\n  background-color: var(--window-body-bg-color);\n  color: var(--window-body-color);\n  position: relative;\n  \n  .centered-content {\n    height: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  }\n  \n  .start-btn {\n    background-color: var(--message-income-bg-color);\n    color: var(--message-income-color);\n    border: none;\n    box-shadow: none;\n    min-width: 150px;\n    min-height: 35px;\n    font-size: 20px;\n    border-radius: 10px;\n    cursor: pointer;\n    outline: none;\n  }\n\n  .messages-list {\n    height: 440px!important;\n    overflow: auto;\n    position: relative;\n    \n    &.has-files {\n      height: 420px!important;\n    }\n  }\n  \n  .unread-messages {\n    position: absolute;\n    left: 0;\n    right: 0;\n    bottom: 5px;\n    margin: 0 auto;\n    width: 150px;\n    text-align: center;\n    background: white;\n    color: black;\n    padding: 5px;\n    border-radius: 10px;\n    animation-duration: .5s;\n    animation-name: fly-in;\n    cursor: pointer;\n    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);\n  }\n  \n  .file-list {\n    padding: 0 10px;\n    font-size: 13px;\n    height: 20px;\n    display: flex;\n    \n    .file-item {\n      display: inline-flex;\n      align-items: center;\n      margin-right: 8px;\n  \n      .file-name {\n        margin: 0 3px;\n      }\n      \n      .delete-file {\n        font-size: 8px;\n        cursor: pointer;\n      }\n    }\n  }\n  \n  .type-message {\n    height: 50px;\n    position: relative;\n    padding: 5px;\n    \n    &.disabled {\n      opacity: 0.5;\n      \n      .send-btn {\n        cursor: default;\n      }\n    }\n    \n    &.has-load-btn {\n      textarea {\n        width: calc(100% - 135px);\n        padding: 10px 80px 10px 55px;\n      }\n    }\n    \n    textarea {\n      color: var(--textarea-color);\n      background-color: var(--textarea-bg-color);\n      font-size: var(--textarea-font-size);\n      font-family: var(--textarea-font-family);\n      width: calc(100% - 105px);\n      height: 30px;\n      border: none;\n      padding: 10px 50px 10px 55px;\n      position: relative;\n      outline: none;\n      overflow: hidden;\n      resize: none;\n      border-radius: 10px;\n      \n      &::placeholder{\n        color: var(--textarea-placeholedr-color);\n        font-size: var(--textarea-font-size);\n        font-family: var(--textarea-font-family);\n      }\n    }\n  \n    .load-btn,\n    .send-btn {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      position: absolute;\n      top: 10px;\n      width: 40px;\n      height: 40px;\n      border: none;\n\n      &, &:focus, &::-moz-focus-inner {\n        border: none;\n        outline: none!important;\n      }\n    }\n    \n    .load-btn {\n      right: 50px;\n      font-size: 18px;\n      \n      label {\n        cursor: pointer;\n      }\n    }\n\n    .send-btn {\n      color: var(--send-btn-color);\n      background-color: var(--send-btn-bg-color);\n      outline: none;\n      right: 10px;\n      border-radius: 10px;\n      cursor: pointer;\n      font-size: 20px;\n    }\n\n    .menu-btn {\n      left: 10px;\n      z-index: 5;\n      background-color: transparent;\n      cursor: pointer;\n    }\n  }\n}\n\n.window-footer {\n  height: var(--window-footer-height);\n  background-color: var(--footer-bg-color);\n  color: var(--footer-color);\n  font-size: var(--footer-font-size);\n  font-family: var(--footer-font-family);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 0 10px;\n  \n  a {\n    color: var(--footer-color);\n    text-decoration: none;\n  }\n}\n\n.context-menu {\n  background: var(--footer-bg-color);\n  position: absolute;\n  bottom: 55px;\n  width: 210px;\n  border-radius: 4px;\n  margin: 5px;\n\n  ul {\n    padding: 10px;\n    margin: 0;\n\n    li {\n      color: var(--window-body-color);\n      cursor: pointer;\n      list-style-type: none;\n\n      i {\n        margin-right: 10px;\n        font-size: 14px;\n        vertical-align: middle;\n        display: inline-block;\n\n      }\n    }\n  }\n}\n\n.tc {\n  max-width: calc(100vw - 125px);\n  white-space: nowrap;\n  overflow-x: hidden;\n  margin: 10px 10px 4px 10px;\n  \n  .tc__right,\n  .tc__left {\n    z-index: 99999;\n    position: absolute;\n    top: 46%;\n    color: #ffffff;\n    cursor: pointer;\n    font-size: 18px;\n  }\n\n  .tc__left {\n    left: 2px;\n  }\n\n  .tc__right {\n    right: 2px;\n  }\n  \n  .tc__cards {\n    position: relative!important;\n    padding-bottom: 6px;\n    \n    &__card {\n      border-radius: 4px;\n      background: #fff;\n      overflow: hidden;\n      margin-right: 8px;\n      width: 200px;\n      height: 300px;\n      display: inline-block;\n      white-space: normal;\n      box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);\n    \n      &:first-of-type {\n        border-radius: 15px 4px 4px 15px;\n      }\n    \n      &:last-of-type {\n        border-radius: 4px 15px 15px 4px;\n        margin-right: 0;\n      }\n    \n      &.hasImg {\n        .tc__cards__card__textBlock {\n          height: 50%;\n        }\n      }\n      \n      &__imgBlock {\n        height: 50%;\n        width: 100%;\n      \n        &__img {\n          width: 100%;\n          height: 100%;\n          object-fit: cover;\n        }\n      }\n    \n      &__textBlock {\n        display: flex;\n        flex-direction: column;\n        justify-content: space-between;\n        height: 100%;\n      \n        &__title {\n          font-size: 12px;\n          font-weight: bold;\n          line-height: 1;\n          color: #000;\n          margin: 10px 10px 0;\n        }\n  \n        &__subtitle {\n          font-size: 12px;\n          line-height: 1;\n          color: #000;\n          height: 100%;\n          overflow: auto;\n          margin: 10px;\n        }\n      \n        &__btns {\n          text-align: center;\n          margin: 0 10px 5px;\n          \n          &__btn {\n            font-size: 13px;\n            color: #34a7fc;\n            font-weight: bold;\n            text-transform: uppercase;\n            cursor: pointer;\n            margin: 1px 5px;\n            display: inline-block;\n          }\n        }\n      }\n    }\n  }\n}\n\n.message-modal {\n  position: fixed;\n  left: 10px;\n  right: 10px;\n  top: 70px;\n  bottom: 143px;\n  z-index: 50;\n  background-color: white;\n\n  iframe {\n    width: 100%;\n    height: 100%;\n  }\n}\n\n.message-block {\n  display: flex;\n  margin: