import { Record } from 'immutable';
export const TYPE_DEFAULT = 1;
export const TYPE_SYSTEM = 3;
export const TYPE_BOT = 4;

export const ATTACHMENT_TYPE_IMAGE = 1;
export const ATTACHMENT_TYPE_FILE = 2;
export const ATTACHMENT_TYPE_LOCATION = 3;
export const ATTACHMENT_TYPE_CONTACT = 4;
export const ATTACHMENT_TYPE_CARDS = 5;

export class MessageRecord extends Record({ type: 1, message: "", image: null, info: null, user: null, attachments: []}) {
    getIsIncome() {
        return this.user ||  this.type === TYPE_BOT || this.getIsSystem();
    }

    getIsSystem() {
        return this.type === TYPE_SYSTEM
    }

    getIsOutcome() {
        return !this.getIsIncome();
    }


    get buttons() {
        return _.get(this.info, ['buttons']);
    }

    get images() {
        return _.filter(this.attachments, {type: ATTACHMENT_TYPE_IMAGE})
    }

    get files() {
        return _.filter(this.attachments, {type: ATTACHMENT_TYPE_FILE})
    }

    get cards() {
        return _.filter(this.attachments, {type: ATTACHMENT_TYPE_CARDS})
    }
}



