import {h, Component} from "preact";
import "./style.scss";

export default class Style extends Component {
    render(props) {
        let styles = props.styles.config;
        return (
            <style type='text/css'>
                {`:root {
                    --bubble-size: ${styles.size}px;
                    --bubble-color: ${styles.main_color};
                    --bubble-bg-color: ${styles.secondary_color};
                    
                    --window-position: ${styles.position};
                    --window-width: 350px;
                    --window-height: 600px;
                    --window-header-height: 60px;
                    --window-footer-height: 40px;
                    --window-body-height: 500px;
                    
                    --header-color: ${styles.window_header_color};
                    --header-bg-color: ${styles.window_header_bg};
                    --header-font-size: ${styles.window_header_fs}px;
                    --header-font-family: ${styles.window_header_ff};
                    
                    --window-body-color: ${styles.window_content_color};
                    --window-body-bg-color: ${styles.window_content_bg};
                    
                    --message-income-bg-color: ${styles.chat_answer_bg};
                    --message-income-color: ${styles.chat_answer_color};
                    --message-income-font-size: ${styles.chat_answer_fs}px;
                    --message-income-font-family: ${styles.chat_answer_ff};
                    --message-income-btn-bg-color: ${styles.chat_answer_btn_bg};
                    --message-income-btn-color: ${styles.chat_answer_btn_color};
                    --message-income-btn-font-size: ${styles.chat_answer_btn_fs}px;
                    --message-income-btn-font-family: ${styles.chat_answer_btn_ff};
                    
                    --message-outcome-bg-color: ${styles.chat_message_bg};
                    --message-outcome-color: ${styles.chat_message_color};
                    --message-outcome-font-size: ${styles.chat_message_fs}px;
                    --message-outcome-font-family: ${styles.chat_message_ff};
                    
                    --textarea-bg-color: ${styles.input_bg_color};
                    --textarea-color: ${styles.input_text_color};
                    --textarea-placeholedr-color: ${styles.input_placeholder_text_color};
                    --textarea-font-size: ${styles.input_text_fs}px;
                    --textarea-font-family: ${styles.input_text_ff};
                    
                    --send-btn-bg-color: ${styles.input_send_btn_bg};
                    --send-btn-color: ${styles.input_send_btn_color};
                    
                    --footer-color: ${styles.window_footer_color};
                    --footer-bg-color: ${styles.window_footer_bg};
                    --footer-font-size: ${styles.window_footer_fs}px;
                    --footer-font-family: ${styles.window_footer_ff};
                }
                .speech-bubble-wrapper {
                    animation: ${(styles.button_animation == 'yes') ? 'play 1.5s ease infinite' : 'none'};
                }
                `}
            </style>
        );
    }
}


