import { h, Component } from "preact";
import ActionButton from "./ActionButton";
import Style from "../../styles/Style";
import axios from 'axios';
import Cookies from 'js-cookie'
import _ from 'lodash';
import Message from "./Message";
import { List } from 'immutable';
window.io = require('socket.io-client');
import Echo from 'laravel-echo';
import { Scrollbars } from 'preact-custom-scrollbars';
import classNames from "classnames";
import {MessageRecord} from "../../data/MessageRecord";
import Writing from "./Writing";
import {Howl} from 'howler';

const TYPE_MESSAGE_USER = 1;
const TYPE_MESSAGE_BOT_OPERATOR = 4;

export default class App extends Component {

    static defaultProps = {
        embed: false,
    };

    state = {
        open: !!(this.props.open || this.isEmbed),
        soundNotifications: true,
        loadBtn: true,
        fullScreen: false,
        messages: List(),
        textEnabled: true,
        input: "",
        typingMessage: null,
        botTyping: false,
        newMessage: 0,
        sizeEnough: true,
        file: null,
        showMenu: false,
    };

    get isEmbed() {
        return !!~~this.props.embed
    }

    constructor(props) {
        super(...arguments);

        this.toggleOpen = this.toggleOpen.bind(this);
        this.toggleNotifications = this.toggleNotifications.bind(this);
        this.toggleFullScreen = this.toggleFullScreen.bind(this);

        this.collectAndSendMessage = this.collectAndSendMessage.bind(this);
        this.sendMessage = this.sendMessage.bind(this);
        this.scrollBottom = this.scrollBottom.bind(this);

        this.client = axios.create({
            baseURL: this.props.baseURL,
            headers: {
                common: {
                    Authorization: "Bearer " + Cookies.get(this.getSessionName('token')),
                    ClientOnly: '1'
                }
            }
        });
        this.webhook = axios.create({
            headers: {
                common: {
                    ClientOnly: '1'
                }
            }
        });

        this.sound = new Howl({
            src: [this.props.configs.message_notification]
        });

        window.addEventListener('message', (event) => {
            try {
                const {type, params} = JSON.parse(event.data);
                if(type === 'sizeEnough') {
                    this.updateSizeEnough(true)
                }
                if(type === 'sizeIsNotEnough') {
                    this.updateSizeEnough(false)
                }
                if(type === 'postMessage') {
                    this.sendMessage(params.message, null, params.payload, params.trigger)
                    this.open();
                }
                if(type === 'open') {
                    this.open()
                }    
            } catch (e) {
                
            }
            
        })
    }

    messageAlert() {
        if(this.state.soundNotifications) {
            this.sound.play();
        }
    }

    updateSizeEnough(newValue) {
        if(this.state.sizeEnough !== newValue) {
            this.setState({
                sizeEnough: newValue
            }, () => {
                this.updateSize();
                this.scrollBottom();
            })
        }
    }

    get isFullScreen() {
        return this.state.fullScreen || !this.state.sizeEnough || this.isEmbed
    }

    scrollBottom() {
        if(!this.list) {
            return  false
        }
        if(this.state.open) {
            setTimeout(() => {
                this.setState({newMessages: 0}, () => this.list.scrollToBottom());
            })
        }
    }

    isScrolled() {
        if(!this.list) {
            return  false
        }
        return this.list.getScrollTop() + 60 < this.list.getScrollHeight() - this.list.getClientHeight();
    }

    get textTypeEnabled() {
        let lastMessage = this.state.messages.last();
        return !(lastMessage && lastMessage.buttons)
            && !this.state.typingMessage && this.state.textEnabled
    }

    componentWillMount() {
        document.addEventListener('DOMContentLoaded', () => {
            setTimeout(() => this.updateSize(), 100);
        })
    }

    componentDidMount() {
        this.registerSession();
    }

    collectAndSendMessage() {
        let message = this.state.input;
        let file = this.state.file;

        if(message || file) {
            this.setState({input: "", file: null});
            this.channel.whisper('typing', {
                message: ''
            });

            this.sendMessage(message, file)
        }
    }

    sendMessage(message, file, postback, trigger) {
        return this.setState({textEnabled: false}, () =>  {
            let formData = new FormData();
            formData.append('message', message);
            if(file) {
                formData.append('file', file);
            }
            if(postback) {
                formData.append('postback', postback);
                if(trigger) {
                    formData.append('trigger', trigger);
                }
            }

            this.webhook.post('', formData).then(() => this.setState({textEnabled: true}))
        });
    }

    getSessionName(key) {
        return [
            key,
            this.props.id,
        ].join('|')
    }



    registerSession() {
        this.client.post('/session/create', {
            'url': this.props.currentURL,
            'agentId': this.props.id
        }).then(({data}) => {
            this.authorize(data.id, data.api_token, data.webhook, data.seance);
            this.setState({
                messages: List(data.messages.map((message) => new MessageRecord(message)))
            }, () => {
                this.scrollBottom()
            })
        })
    }
    live() {
        setTimeout(() => {
            this.client.post('/session/live/' + this.seance, {
                'url': this.props.currentURL,
                'agentId': this.props.id
            }).then(() => {
                this.live()
            })
        }, 5e3)
    }

    authorize(id, token, webhook, seance) {
        Cookies.set(this.getSessionName('token'), token);
        this.seance = seance;
        this.client.defaults.headers.common['Authorization'] = "Bearer " + token;
        this.webhook.defaults.headers.common['Authorization'] = "Bearer " + token;
        this.webhook.defaults.baseURL = webhook;

        this.live();

        const EchoServer = new Echo({
            broadcaster: 'socket.io',
            host: this.props.baseURL,
            auth: {
                headers: {
                    Authorization: "Bearer " + token,
                    ClientOnly: '1'
                },
            },
        });

        this.channel = EchoServer.private('Session.' + id)

        this.channel.listen('ClientNewMessage', (e) => {
            const wasScrolled = this.isScrolled();
            let messageRecord = new MessageRecord(e.message);
            if(!messageRecord.getIsOutcome()) {
                this.messageAlert();
            }
            this.setState(({messages}) => ({
                messages: messages.push(messageRecord),
                botTyping: false,
            }), () => {
                if(!wasScrolled || messageRecord.getIsOutcome()) {
                    this.scrollBottom();
                } else {
                    this.setState(({newMessages}) => ({newMessages: newMessages+1}))
                }
            });

                // this.props.onMessage && this.props.onMessage(e.message);


        })
            .listen('BotTyping', () => {
                const wasScrolled = this.isScrolled();
                this.setState({botTyping: true}, () => {
                    if(!wasScrolled) {
                        this.scrollBottom()
                    }
                }) ;
            })
            .listenForWhisper('answering', () => {
                    if (!this.state.typing) {
                        let cb = this.isScrolled() ? () => {
                            this.scrollBottom()
                        } : () => {
                        };
                        this.setState({typing: true}, cb)
                    }
                }
            )
            .listenForWhisper('stop-answering', () => {
                if (this.state.typing) {
                    this.setState({typing: false})
                }
            })
    }


    open() {
        if(!this.state.open) {
            this.setState({open: true}, () => {
                this.scrollBottom();
                this.updateSize();
            });
        }

    }

    postMessage(event, params = {}) {
        window.parent.postMessage(JSON.stringify({event, ...params}), '*');
    }

    updateSize() {
        if(this.state.open && this.isFullScreen) {
            this.postMessage('full-screen');
        } else {
            let body = document.body;
            this.postMessage('resize', {height: body.scrollHeight, width: body.scrollWidth});
        }
    }

    toggleOpen() {
        this.setState(({open}) => ({open: !open, fullScreen: false}), () => {
            this.state.open && this.scrollBottom();
            this.updateSize();
        });
    }

    toggleNotifications() {
        this.setState(({soundNotifications}) => ({soundNotifications: !soundNotifications}));
    }

    toggleFullScreen() {
        this.setState(({fullScreen}) => ({fullScreen: !fullScreen}), () => {
            this.updateSize();
        });
    }

    getMessageImage(message) {
        if(message.user) {
            return message.user.image ? '/storage/'+message.user.image.path : this.props.configs.operator_image
        } else {
            return this.props.configs.bot_image
        }
    }

    renderButton() {
        return (<div onClick={this.toggleOpen} className={'speech-bubble-wrapper'}>
            <i className={'icon-button speech-bubble-icon'}/>
        </div>)
    }

    renderWidget() {
        const {
            logo,
            locale,
            powered_name,
            powered_url
        } = this.props.configs;

        const settings = this.props.configs.styles.config;
        const state = this.state;
        const messages = state.messages.toArray();
        const hasMassages = !!messages.length;

        let lastMessage;

        if(state.botTyping) {
            lastMessage = new MessageRecord({type: TYPE_MESSAGE_BOT_OPERATOR, message: <Writing/>})
        } else {
            lastMessage = messages.pop();
        }
        return (
            <div className={classNames("window-wrapper", {"fullscreen": this.isFullScreen})}>
                <div className="window-header">
                    <div className="name">
                        <img src={logo} alt={settings.header_title[locale]} className={'logo-img'}/>
                        <span className={'title'}>{settings.header_title[locale]}</span>
                    </div>
                    <div className="configs">
                        <ActionButton
                            icon={state.soundNotifications ? "icon-bell-regular" : "icon-bell-slash-regular"}
                            onClick={this.toggleNotifications}
                        />
                        {
                            (state.sizeEnough && !this.isEmbed) && <ActionButton
                                icon={"icon-window-minimize"}
                                onClick={this.toggleFullScreen}
                            />

                        }
                        {( !this.isEmbed) && <ActionButton
                            icon={"icon-times-solid"}
                            onClick={this.toggleOpen}
                        />}
                    </div>
                </div>

                <div className="window-body">
                    <div className={'relative'}>
                        <Scrollbars
                            className={classNames("messages-list",  {"has-files": state.file})}
                            ref={(el) => this.list = el}
                            renderThumbVertical={props => <div {...props} className="scroll-thumb-vertical"/>}
                            renderTrackVertical={props => <div {...props} className="scroll-track-vertical"/>}
                            renderView={props => <div {...props} className="scroll-view"/>}
                        >
                            {hasMassages ?  <div>
                            {
                                messages.map((message) => <Message
                                    key={message.id}
                                    message={message}
                                    image={this.getMessageImage(message)}
                                    sendMessage={this.sendMessage}
                                    showImage={true}
                                />)
                            }
                            {lastMessage && <Message
                                key={lastMessage.id}
                                allowButton
                                sendMessage={this.sendMessage}
                                message={lastMessage}
                                image={this.getMessageImage(lastMessage)}
                                showImage={true}
                            />}
                            {
                                this.state.typingMessage && <div>...</div>
                            }
                            </div> : <div className="centered-content">
                                <button className="start-btn" onClick={() => this.sendMessage('start')}>{settings.start_btn[locale]}</button>
                            </div>}
                        </Scrollbars>
                        {
                            !!state.newMessages && <div className={'unread-messages'} onClick={this.scrollBottom}>
                                Unread messages ({state.newMessages})
                            </div>
                        }
                    </div>

                    {
                        state.file && <div className={'file-list'}>
                            <div className={'file-item'}>
                                <i className={'icon-file-image-solid'}/>
                                <span className={'file-name'}>{
                                    _.truncate(state.file.name, {
                                        length: 25
                                    })
                                }</span>
                                <i onClick={() => this.setState({file: null})} className={'delete-file icon-times-solid'}/>
                            </div>
                        </div>
                    }

                    {
                        state.showMenu && <div className={'context-menu'}>
                            <ul>
                                <li
                                    onClick={() => {
                                        this.setState({
                                            showMenu: false
                                        })
                                        this.sendMessage('restart')
                                    }}

                                ><i className={'icon-restart'}/>Restart</li>
                            </ul>
                        </div>
                    }
                    {hasMassages &&
                        <div className={classNames("type-message",  {'disabled': !this.textTypeEnabled, 'has-load-btn': state.loadBtn})}>
                            <button className={'load-btn menu-btn'} onClick={() => this.setState(({showMenu}) => ({showMenu: !showMenu}))}>

                                    <i className={'icon-menu'}/>
                            </button>
                            <textarea
                                disabled={!this.textTypeEnabled}
                                value={state.input}
                                onInput={(e) => {
                                    if(this.enter) return;
                                    const input = e.target.value;
                                    this.channel.whisper('typing', {
                                        message: input
                                    });
                                    this.setState({input})
                                }}
                                placeholder={settings.input_placeholder[locale]}
                                onKeyDown={(e) => {
                                    this.enter = false;
                                    if (e.keyCode === 13 && !e.shiftKey) {
                                        this.enter = true;
                                        e.stopPropagation();
                                        this.collectAndSendMessage();
                                    }
                                }
                                }
                            />
                                {state.loadBtn &&
                                <div className={'load-btn'}>
                                    <label htmlFor="file"><i className={'icon-paperclip-solid'}/></label>
                                    <input disabled={!this.textTypeEnabled} hidden onChange={(e) => {
                                        this.setState({file: e.target.files[0]})
                                    }} name="file" id="file" type="file"/>
                                </div>
                                }
                                <button disabled={!this.textTypeEnabled} className={'send-btn'} onClick={this.collectAndSendMessage}><i className={'icon-telegram-plane'}/></button>
                        </div>
                    }
                </div>

                <div className="window-footer">
                    <div className="powered">{this.props.translates.poweredBy} <a target="_parent" href={powered_url}>{powered_name}</a></div>
                </div>
            </div>
        )
    }

    render() {

        return <div>
            <Style 
                styles={this.props.configs.styles}
            />
            {this.state.open && this.renderWidget()}
            <div />
            {this.renderButton()}
        </div>
    }
}



